import React, { useState } from "react";
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
  Row,
  Col,
} from "reactstrap";
import testimonial from "../../assets/img/testimonial/2.jpg";

const items = [
  {
    src: "https://picsum.photos/id/123/1200/400",
    // altText: "Slide 1",
    // caption: "Slide 1",
    key: 1,
  },
  {
    // src: "https://picsum.photos/id/456/1200/400",
    // altText: "Slide 2",
    // caption: "Slide 2",
    // key: 2,
  },
  {
    // src: "https://picsum.photos/id/678/1200/400",
    // altText: "Slide 3",
    // caption: "Slide 3",
    // key: 3,
  },
];

function Testimonials(args) {
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  };

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  };

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex);
  };

  const slides = items?.map((item, i) => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={item?.src}
      >
        {/* <Row>
        <Col lg="8" md="8" xl="8" xs="6">ss</Col>
        <Col lg="8" md="8" xl="8" xs="6">bb</Col>
      </Row> */}
        <div
          style={{
            height: "250px",
            backgroundColor: "white",
            border: "1px solid black",
          }}
          className="d-flex p-4"
        >
          <div
            className="container carasouldleft"
            style={{ width: "80%", color: "black" }}
          >
            <p className="paragraphtxt">
              <span style={{ fontSize: "40px", color: "black" }}>‘‘</span>
              With MMBAIC Trainers were able to marry the two together,
              technology and consultant soft skills. We're thankful that once
              they got in and took their business training course that they
              efficiently moved over to Consulting courses to help drive their
              career forward.
            </p>
          </div>
          <div style={{ width: "20%", color: "black" }}>
            <div>
              <img
                style={{ borderRadius: "50%" }}
                src={testimonial}
                alt="images"
              />
            </div>
            <div>
              <h6>Anand Mekala</h6>
              <h6>IT HEAD ,SM Express Logistics </h6>
              <h6>Mumbai-Maharastra </h6>
            </div>
            <div>
              <img />
            </div>
          </div>
        </div>
        {/* <img src={item.src} alt={item.altText} /> */}
        <CarouselCaption
          captionText={item.caption}
          captionHeader={item.caption}
        />
      </CarouselItem>
    );
  });

  return (
    <>
      <Row>
        <Col>
          <div className="container mb-4">
            <Carousel
              activeIndex={activeIndex}
              next={next}
              previous={previous}
              {...args}
            >
              <CarouselIndicators
                items={items}
                activeIndex={activeIndex}
                onClickHandler={goToIndex}
              />
              {slides}
              <CarouselControl
                direction="prev"
                directionText="Previous"
                onClickHandler={previous}
              />
              <CarouselControl
                direction="next"
                directionText="Next"
                onClickHandler={next}
              />
            </Carousel>
          </div>
        </Col>
      </Row>
    </>
  );
}

export default Testimonials;
